<template>
  <div class="updateInfo-box">
    <el-form  class="demo-ruleForm">
    </el-form>
    <div style="width:1000px;margin-left:10px;margin-top:40px">
      <el-table :data="tableData" style="width: 100%;">
            <el-table-column prop="orderNum"  width="100px" label="ID">
              <template scope="scope">
                <div slot="reference" class="name-wrapper " >
                  <el-button type="text" @click="goToDetail(scope.$index, scope.row)">{{ scope.row.orderNum }}</el-button>
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="infos" width="350px" label="">
              <template scope="scope">
                <div slot="reference" class="name-wrapper omit">
                  
                </div>
              </template>

            </el-table-column>
            <el-table-column prop="ostime" label="">

              <template scope="scope">
                <div slot="reference" class="name-wrapper">
                  {{ scope.row.ostime }}
                </div>
              </template>

            </el-table-column>
            <el-table-column prop="totalPrice" label="">

              <template scope="scope">
                <div slot="reference" class="name-wrapper">
                  {{ scope.row.totalPrice }}
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="ostate" label="">

              <template scope="scope">
                <div slot="reference" class="name-wrapper">
                  {{ scope.row.ostate }}
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="" label="">

              <template scope="scope">
                <div slot="reference" class="name-wrapper">
                  <el-button type="danger" @click="beforeDel(scope.$index, scope.row)"></el-button>
                </div>
              </template>
            </el-table-column>
          </el-table>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import config from './../../../publicAPI/config'
  import { Message } from 'element-ui';//

  export default {
    components: {
    },
    data() {
    return {
      rootURL: config.JXURL,
      balance: '--',//
      activeName: 'first',
      tableData: [],
    };
  },
  methods: {
      beforeDel(index,row) {
        let that = this ;
        const h = this.$createElement;
        this.$msgbox({
          title: '',
          message: h('p', null, [
            h('span', null, ' '),
            h('i', { style: 'color: teal' }, row.orderNum),
              h('span', null, '  '),
          ]),
          showCancelButton: true,
          confirmButtonText: '!',
          cancelButtonText: '',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true;
              instance.confirmButtonText = '...';
              setTimeout(() => {
                done();
                setTimeout(() => {
                  instance.confirmButtonLoading = false;
                }, 300);
              }, 3000);
              that.delOrder(index,row);
            } else {
              done();
            }
          }
        }).then(action => {

        });
      },
    delOrder(index,row){
      console.log(row.orderNum)
      let that = this ;
      var querystring = require('querystring');//Json
      axios.post(that.rootURL +'/deleteOrders.do',
         querystring.stringify({
           oid:row.orderNum,
         })
        )
        .then(function(res){
        if(res.data.status){
          Notification.success({
                    title: '',
                    message: res.data.msg,
                    offset: 65,
                      duration:2000
                  })
        } else {
          Notification.error({
                    title: '',
                    message: res.data.msg,
                    offset: 65,
                      duration:2000
                  })
          // window.location = '#/tradeSystem/login'
        }
        })
        .catch(function(error){
          Message.error('');
        });
    },
    goToDetail(index,row){
      let that = this;
      that.$router.push({path:'/orderdetail/'+row.orderNum})
    },
    getRecord() {
        let that = this
        axios.get(that.rootURL+'/queryOrders.do')
          .then(function(res){
            that.tableData = []
            for( var item of res.data ) {
                var str = '';
                for( var ite of res.data ) {

                }
              that.tableData.push({
                orderNum: item.oid,
                oaddress: item.oaddress,
                oname: item.oname,
                totalPrice: item.ototalprice,
                ostime: item.ostime,
                ostate: item.ostate,

                  // infos:item.id,
              })
          }
          })
          .catch(function(error){
            console.error(error)
          })
      },
  },
  created(){
    this.getRecord()
  }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-row{
    width: 800px;
  }
  .el-submenu .el-menu-item{
    height: 35px;
    line-height: 35px;
  }
  .bodyBG{
    background-size: 100% 508px;
    min-height: 508px;
    width:1080px
  }
  .hint{
    width: 100%;
    height:50px;
  }
  .el-tab-pane{
    margin: 40px 300px;
  }
  .el-form-item{
    margin-bottom: 30px;
  }
  big{
    color: #20a0ff;
  }
  .block{
    margin-top: 25px;
    text-align: right;
  }

  .omit {
      width: 320px;
      overflow: hidden;
      vertical-align: top;
      text-overflow: ellipsis;
      white-space: nowrap;
      /**/
  }

       .updateInfo-box{
       	float: left;
       	border-radius: 4px;
       	background:rgba(255, 255, 255,0.7);
         position:relative;
       	padding:50px;
        margin-left:-100px;
        margin-top:0px;
       	box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.15);
       }
</style>



